import React from "react";
import sampleIMG from "./assets/EDCAN_2020.jpg";

export default function Slide({ title, content, unit }) {
  let unitVar = ["", "text-sm "];

  if (unit === "p") {
    unitVar[1] += "text-red-500";
    unitVar[0] = "Pixel";
  } else if (unit === "a") {
    unitVar[1] += "text-blue-500";
    unitVar[0] = "Atelier";
  }

  return (
    <div className="w-2/3">
      <img className="select-none rounded-xl" src={sampleIMG} alt="포트폴리오 이미지" />
      <div className="mt-4">
        <p className={unitVar[1]}>{unitVar[0]}</p>
        <p className="mt-1 font-medium text-xl">{title}</p>
        <p className="text-base mt-1 font-light text-gray-500">{content}</p>
      </div>
    </div>
  );
}
