import React from "react";
import sampleIMG from "./assets/EDCAN_2020.jpg";
import xIcon from "./assets/x.svg";

const hideBtn = () => {
  document.getElementById("popup").className =
    "z-0 bg-black bg-opacity-80 fixed inset-0 hidden items-center w-screen h-screen";
    setTimeout(() => {
      document.getElementById("popup").className =
        "transition-opacity opacity-0 z-0 bg-black bg-opacity-80 fixed inset-0 flex items-center w-screen h-screen";
    }, 100);
};

export default function Popup() {
  return (
    <div
      id="popup"
      className="transition-opacity opacity-0 z-0 bg-black bg-opacity-80 fixed inset-0 hidden items-center w-screen h-screen"
    >
      <div className="bg-black text-white w-5/6 xl:w-4/6 2xl:w-7/12 rounded-2xl mx-auto">
        <div className="sticky h-full w-full bg-opacity-5 bg-white px-6 py-8 sm:px-16 sm:py-20 rounded-2xl">
          <p className="text-sm font-medium text-red-400">Pixel</p>
          <p className="text-xl font-medium mt-2">Title, 타이틀</p>
          <img
            className="mt-6"
            src={sampleIMG}
            alt="포트폴리오 더보기 이미지"
          />
          <p className="text-white mt-4">content</p>
          <div
            onClick={hideBtn}
            className="absolute sm:p-1 top-7 right-5 sm:top-16 sm:right-16 z-50 w-6 sm:w-8"
          >
            <img src={xIcon} alt="x-button" />
          </div>
        </div>
      </div>
    </div>
  );
}
