import React from "react";
import { Link } from "react-router-dom";
import edcanImg from "./assets/EDCAN_LOGO.svg"

const Foot = () => (
  <footer className="py-8 bg-gray-100 content-center text-justify text-gray-600">
    <div className="flex flex-row flex-wrap justify-center w-screen">
      <Link className="w-32 flex" to="/">
        <img src={edcanImg} alt="에드캔 로고" />
      </Link>
      <div className="mt-4 sm:mt-0 text-center sm:text-left px-8 flex items-center">
        <p className="font-light">
          서울 용산구 원효로97길 33-4 (청파동3가, 선린인터넷고등학교)
          <br />
          Copyright ©EDCAN. All rights reserved.
        </p>
      </div>
    </div>
  </footer>
);

export default Foot;
